	/**
	 *  ```
	 *  body / <= Screen
	 *  ```
	 **/
	body() {
		return [ this.Screen() ] as readonly any[]
	}

	/**
	 *  ```
	 *  Screen $piterjs_screen
	 *  	place <= place
	 *  	Close null
	 *  	content /
	 *  		<= Logo
	 *  		<= Join
	 *  		<= Patreon
	 *  ```
	 **/
	@ $mol_mem
	Screen() {
		return (( obj )=>{
			obj.place = () => this.place()
			obj.Close = () => null as any
			obj.content = () => [ this.Logo() , this.Join() , this.Patreon() ] as readonly any[]
			return obj
		})( new this.$.$piterjs_screen(  ) )
	}

